
body{font-family:Helvetica Neue, Arial, sans-serif;font-size:14px;color:#444
}
table{border:2px solid #42b983;border-radius:3px;background-color:#fff
}
th{background-color:#42b983;color:rgba(255,255,255,0.66);cursor:pointer;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
td{background-color:#f9f9f9
}
th,td{min-width:120px;padding:10px 20px
}
th.active{color:#fff
}
th.active .arrow{opacity:1
}
.arrow{display:inline-block;vertical-align:middle;width:0;height:0;margin-left:5px;opacity:0.66
}
.arrow.asc{border-left:4px solid transparent;border-right:4px solid transparent;border-bottom:4px solid #fff
}
.arrow.dsc{border-left:4px solid transparent;border-right:4px solid transparent;border-top:4px solid #fff
}
button{text-decoration:none;text-align:center;padding:11px 32px;border:solid 1px #42b983;border-radius:19px;font:18px Arial, Helvetica, sans-serif;font-weight:bold;color:#42b983;background:#ffffff;margin-bottom:30px;margin-top:30px
}
.delete{background-color:#edf6ff;font:12px Arial, Helvetica, sans-serif;font-weight:bold
}



